
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import {
mapState, mapMutations, mapGetters, mapActions
} from "vuex";

import * as types  from '@/store/type';

export default {
name: 'Login',
components: {
ViewBox, XHeader, Box, XInput, XButton, Grid, GridItem,
Group, GroupTitle, Confirm, InlineXNumber, Tabbar, TabbarItem
},
data(){
return {
mobile: '',
passwd: '',
code: '',
codeVal: '获取验证码',
codeFlog: false,
wait: 60,
}
},
computed: {
...mapGetters([
'cart_goods_list'
])
},
created(){},
mounted(){},
methods: {
login(){














































if (this.mobile!=="") {
this.$http.get('http://139.199.168.158:8080/admin/Login',{params:{username: this.mobile,password: this.passwd}}).then(res => {
console.log(res)
if (res.data.code === 1) {
let data = res.data.data[0];
this.$store.commit(types.LOGIN, data.type);
this.$store.commit(types.USERNAME, data.realname);
this.$store.commit(types.USERID, data.userid);

let redirect = ''
console.log(data)
switch(data.type){
case 'teacher':
redirect = '/home-teacher-centre';
break;
case 'student':
redirect = '/home-student-centre';
break;
case 'parent':
redirect = '/home-student-centre';
break;
case 'supervisor':
redirect = '/home-supervisor-home';
break;
}
this.$router.push({

path: redirect
});
}
else {
this.$vux.alert.show({
title: '登录失败',
content: '用户名或密码错误',
})
}
}).catch(err=>{
console.log(err);
});
}else{
this.$vux.toast.show({
type:'cancel',
text:'手机号有误'
});
}
},
signUp(){
this.$router.push({

path: '/sign-up'
});
},
getCode(){
if (this.codeFlog)return;
if(this.mobile !=="" && this.mobile.length===11){
this.$http({
url:api.getCode_api + this.mobile + '/2/sms',
method:'post',
data:{},
params:{}
}).then(res => {
if (res.data.code === 200) {
this.time();
}
if(res.data.code === 500){
this.$vux.toast.show({
type:'cancel',
text:res.data.message
});
}
});
}else{
this.$vux.toast.show({
type:'cancel',
text:'手机号有误'
});
}
},
time() {
if (this.wait === 0) {
this.codeFlog = false;
this.codeVal = "获取验证码";
this.wait = 60;
} else {
this.codeFlog = true;
this.codeVal = "重新发送(" + this.wait + ")";
this.wait--;
setTimeout( () =>{
this.time()
}, 1000);
}
},
}
}



